<template>
  <div class="details" v-if="list && list.aut_photo" >
    <!-- <div>我是详情页面</div> -->
    <!--    封装了tab组件-->
    <tab title="文章详情" @left="onClickLeft" :class="[{'animate__animated animate__fadeIn': controlAnimate}]" />
    <div class="py-5 px-3">
      <h4 class="text-4xl font-bold font-mono text-pink-400 break-all">{{ list.title }}</h4>
      <div class="flex justify-between items-center py-5 px-2 mb-3 shadow-md rounded-2xl">
        <div class="flex justify-between w-1/2 items-center" v-if="list && list.aut_photo">
          <img :src="list.aut_photo" alt="" class="w-20 h-20 rounded-full"/>
          <div class="text-coolGray-400 font-serif text-base flex flex-col justify-between items-center h-12 w-full">
            <div class="">{{ list.aut_name }}</div>
            <div class="">{{ list.pubdate | dayfilter }}</div>
          </div>
        </div>
        <div
          :class="['rounded-2xl bg-pink-500 px-3 h-10 text-white flex justify-center items-center', { active: isfolowed === true || list.is_followed }]"
          v-if="list.is_followed"
          @click="changeShow"
        >
          <van-icon name="like-o" />
          <span class="px-1">已关注</span>
        </div>
        <div class="rounded-2xl bg-lightBlue-500 px-3 h-10 text-white flex justify-center items-center" v-else @click="blank">
          <van-icon name="aim" />
          <span class="px-1">关注</span>
        </div>
      </div>
      <div class="py-5 font-bold font-mono text-base break-all whitespace-pre-wrap abc" v-html="list.content"></div>
    </div>
    <div class="flex justify-evenly items-center mt-6">
      <div :class="['w-20 h-20 rounded-full bg-rose-500 flex justify-center items-center font-mono text-white', { active: list.attitude === 1 }]" @click="dianZan">点赞</div>
      <div :class="['w-20 h-20 rounded-full bg-lightBlue-500 flex justify-center items-center font-mono text-white', { active: list.attitude === 0 }]" @click="like">不喜欢</div>
    </div>
    <div>
      <commmen :list="list" :id="id"></commmen>
    </div>
  </div>
</template>

<script>
import {
  getDetailsApi,
  unfollowUsersApi,
  followUsersApi,
  likingsApi,
  deleteLikingApi,
  distasteApi,
  likeApi
} from '../../../api/details'
import commmen from './commmen.vue'
import Tab from '@/components/Tab'
export default {
  components: { Tab, commmen },
  data () {
    return {
      id: this.$route.params.id, // 文章id
      list: [],
      comment: '', // 评论内容
      isfolowed: false,
      controlAnimate: true
    }
  },
  created () {
    this.getDetails()
  },
  methods: {
    onClickLeft () {
      this.$router.push('/index')
    },
    // 获取文章详情
    async getDetails () {
      const res = await getDetailsApi(this.id)
      this.list = res.data
      this.aut_id = this.list.aut_id // 作者id
      // this.onLoad()
    },

    // 点击取消关注
    async changeShow () {
      this.isfolowed = false
      this.list.is_followed = false
      try {
        const res = await unfollowUsersApi(this.aut_id)
        console.log(res)
      } catch {
        console.log(1111)
      }
      // this.$toast.info(res.message)
    },
    // 点击关注
    async blank () {
      this.isfolowed = true
      this.list.is_followed = true
      const res = await followUsersApi({
        target: this.aut_id
      })
      console.log(res)

      // this.$toast.info(res.message)
    },

    // 取消点赞&点赞
    async dianZan () {
      if (this.list.attitude === -1) {
        this.list.attitude = 1
        const res = await likingsApi({
          target: this.id
        })
        this.$toast.success(res.message)
      } else if (this.list.attitude === 1) {
        this.list.attitude = -1
        const res2 = await deleteLikingApi(this.id)
        this.$toast.success(res2.message)
      }
    },
    async like () {
      if (this.list.attitude === -1 || this.list.attitude === 1) {
        this.list.attitude = 0
        const res = await likeApi(this.list.art_id)
        this.$toast.success(res.message)
      } else if (this.list.attitude === 0 || this.list.attitude === 1) {
        this.list.attitude = -1
        const res2 = await distasteApi({
          target: this.list.art_id
        })
        this.$toast.success(res2.message)
      }
    }
  }
}
</script>
<style lang="less">
.abc {
  * {
    padding: 0;
    margin: 0;
    white-space: pre-wrap; /* css3.0 */
    word-break: break-all;
  }
}
img {
  width: 100%;
}
</style>
