import _fetch from '@/utils/axios'

// 获取文章详情
function getDetailsApi (id) {
  return _fetch({
    url: `/v1_0/articles/${id}`,
    headers: {
      contentType: 'application/json'
    }
  })
}
// 获取评论数据
function getCommentsApi (data) {
  return _fetch({
    url: '/v1_0/comments',
    headers: {
      contentType: 'application/json'
    },
    params: data
  })
}
// 取消关注
function unfollowUsersApi (id) {
  return _fetch({
    url: `/v1_0/user/followings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 点击关注
function followUsersApi (data) {
  return _fetch({
    url: '/v1_0/user/followings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}
// 对文章点赞
function likingsApi (data) {
  return _fetch({
    url: '/v1_0/article/likings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

// 取消对文章点赞
function deleteLikingApi (id) {
  return _fetch({
    url: `/v1_0/article/likings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}

// 对文章不喜欢
function distasteApi (data) {
  return _fetch({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

// 取消对文章不喜欢
function likeApi (id) {
  return _fetch({
    url: `/v1_0/article/dislikes/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 添加评论&评论回复
function addSendApi (data) {
  return _fetch({
    url: '/v1_0/comments',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}
// 对评论或评论回复点赞
function addcommsApi (data) {
  return _fetch({
    url: '/v1_0/comment/likings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

function deleLikes (id) {
  return _fetch({
    url: `/v1_0/comment/likings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}

export {
  getDetailsApi,
  getCommentsApi,
  unfollowUsersApi,
  followUsersApi,
  likingsApi,
  deleteLikingApi,
  distasteApi,
  likeApi,
  addSendApi,
  addcommsApi,
  deleLikes
}
