<template>
  <div class="mt-6">
    <div class="">
      <div v-if="comment_data">
        <van-list v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad">
          <div class="flex justify-between items-start px-5 pt-4 border-b border-coolGray-200 border-opacity-40"
            v-for="(item, index) in comment_data"
            :key="index">
            <div class="w-16 h-16 rounded-full overflow-hidden" v-if="item && item.aut_photo">
              <img :src="item ? item.aut_photo : null" alt="" />
            </div>
            <div class="flex flex-col w-3/4 ">
              <div class="flex justify-between">
                <div class="text-xl pt-1 leading-4 text-gray-400">{{ item.aut_name }}</div>
                <div class="flex justify-center items-center">
                  <van-icon
                      name="like"
                      size="18"
                      :class="['text-lightBlue-500 pr-1', {'text-rose-500': item.is_liking}]"
                      @click="likes(item)"/>
                  <span>{{ item.is_liking ? item.like_count + 1 : item.like_count}}</span>
                </div>
              </div>
              <div class="flex flex-wrap py-3 items-center">
                <h1 class="font-bold break-all">评论：</h1>
                <p class="font-normal text-sm break-all">{{ item.content }}</p>
              </div>
              <div class="flex font-mono text-coolGray-300">
                <div class="pr-4">{{ item.pubdate | dayfilter }}</div>
                <div class="text-coolGray-600" @click="reply(item)">
                  {{ item.reply_count }} 回复
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </div>
      <div v-else class="">没有更多了</div>
    </div>
    <div class="fixed bottom-0 w-full h-11 rounded-2xl font-mono text-base border border-coolGray-200 flex justify-between px-3 focus-within:ring-1 focus-within:ring-coolGray-200">
      <input type="text" v-model="comment" class="w-full h-full " />
      <button @click="send" class="text-lightBlue-600 focus:outline-none focus:text-rose-500 w-20 bg-white">发送</button>
    </div>
    <van-popup v-model="showPopup" position="bottom" :style="{ height: '80%' }">
      <div class="px-2">
        <div class="font-mono text-2xl font-black py-2 px-5 text-coolGray-400 border-2 border-t-0 rounded-md">当前评论</div>
        <div class="flex justify-between items-start px-5 pt-4 border-b border-coolGray-200 border-opacity-40"
                 v-for="(value1, index) in reply_data"
                 :key="index">
              <div class="w-16 h-16 rounded-full overflow-hidden" v-if="value1 && value1.aut_photo">
                <img :src="value1.aut_photo" alt="" />
              </div>
              <div class="flex flex-col w-3/4 ">
                <div class="flex justify-between">
                  <div class="text-xl pt-1 leading-4 text-gray-400">{{ value1.aut_name }}</div>
                  <div class="flex justify-center items-center">
                    <van-icon
                      name="like"
                      size="18"
                      :class="['text-lightBlue-500 pr-1', {'text-rose-500': value1.is_liking}]"
                      @click="likes(value1)"/>
                    <span>{{ value1.is_liking ? value1.like_count + 1 : value1.like_count }}</span>
                  </div>
                </div>
                <div class="flex flex-wrap py-3 items-center">
                  <h1 class="font-bold break-all">评论：</h1>
                  <p class="font-normal text-sm break-all">{{ value1.content }}</p>
                </div>
                <div class="flex font-mono text-coolGray-300">
                  <div class="pr-4">{{ value1.pubdate | dayfilter }}</div>
                  <div class="text-coolGray-600" @click="reply(value1)">
                    {{ value1.reply_count }} 回复
                  </div>
                </div>
              </div>
            </div>
        <div class="fixed bottom-0 w-full h-11 rounded-2xl font-mono text-base border border-coolGray-200 flex justify-between px-3 focus-within:ring-1 focus-within:ring-coolGray-200">
          <input type="text" v-model="reply_value" class="w-full h-full " />
          <button @click="reply_comm" class="text-lightBlue-600 focus:outline-none focus:text-rose-500 w-20 bg-white">发送</button>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  addSendApi,
  getCommentsApi,
  deleLikes,
  addcommsApi
} from '../../../api/details'
export default {
  name: 'comment',
  props: ['list', 'id'],
  data () {
    return {
      comment: '', // 评论内容
      comment_data: [], // 文章数据
      total: 0,
      last: undefined,
      isfolowed: false,
      loading: false,
      finished: false,
      showPopup: false,
      reply_data: [],
      pinglunID: 0,
      reply_value: ''
    }
  },
  methods: {
    // 上拉加载&评论数据
    async onLoad () {
      const res = await getCommentsApi({
        type: 'a',
        source: +this.list.art_id,
        offset: this.last
      })
      // 总数
      this.total = res.data.total_count
      // 评论数据
      this.last = res.data.last_id
      this.comment_data.push(...res.data.results)
      this.total = res.data.total_count
      this.loading = false

      if (this.comment_data.length >= this.total) {
        this.finished = true
      }
    },
    // 添加新评论
    async send () {
      const res = await addSendApi({
        target: this.list.art_id,
        content: this.comment.trim()
      })
      this.$toast.success(res.message)
      this.comment_data.unshift(res.data.new_obj)
      this.comment = ''
    },
    async reply (item) {
      this.pinglunID = item.com_id.c.join('')
      this.showPopup = true
      this.reply_data = []
      const res = await getCommentsApi({
        type: 'c',
        source: this.pinglunID
      })
      this.reply_data.push(...res.data.results)
    },
    // 评论回复
    async reply_comm () {
      const res = await addSendApi({
        target: this.pinglunID,
        art_id: this.id,
        content: this.reply_value
      })
      this.reply_data.unshift(res.data.new_obj)
      this.reply_value = ''
      this.reply_data = []
      this.showPopup = false
    },
    async likes (value) {
      if (value.is_liking === true) {
        value.is_liking = false
        const res = await deleLikes(value.aut_id.c.join(''))
        console.log(res)
      } else {
        value.is_liking = true
        const res2 = await addcommsApi({
          target: value.aut_id.c.join('')
        })
        console.log(res2)
      }
    }
  },
  mounted () {
    this.onLoad()
  }
}
</script>
